import React from "react";

const educationContent = [
  {
    year: "2016 - 2020",
    degree: "Bachelor Degree",
    institute: "UNIVERSITI TUN HUSSEIN ONN MALAYSIA",
    details: `Learn new programming languages including HTML, CSS, C#, Java, PHP, and others by enrolling at UTHM for a bachelor's in computer science (Web Technology). The challenge of developing an Android Studio app for my final year project piqued my interest in the subject of programming.`,
  },
  {
    year: "2015 - 2016",
    degree: "Matriculation ",
    institute: "Penang Matriculation College",
    details: `Enroll in a one-year matriculation programme at Penang Matriculation College (KMPP), where I learned about computer science for the first time through learning C++ and developed a strong interest in programming.`,
  },

];

const Education = () => {
  return (
    <ul>
      {educationContent.map((val, i) => (
        <li key={i}>
          <div className="icon">
            <i className="fa fa-briefcase"></i>
          </div>
          <span className="time open-sans-font text-uppercase">{val.year}</span>
          <h5 className="poppins-font text-uppercase">
            {val.degree}
            <span className="place open-sans-font">{val.institute}</span>
          </h5>
          <p className="open-sans-font">{val.details}</p>
        </li>
      ))}
    </ul>
  );
};

export default Education;
