import React from "react";

const SocialShare = [
  {
    iconName: "fa fa-facebook",
    link: "https://www.facebook.com/khairul.imran11/",
  },
  { iconName: "fa fa-twitter", link: "https://twitter.com/khairulimran260/" },
  {
    iconName: "fa fa-instagram",
    link: "https://www.instagram.com/_khairul_imran/",
  },
  { iconName: "fa fa-wordpress", link: "https://thedailyinfowp.com/" },
];

const Social = () => {
  return (
    <ul className="social list-unstyled pt-1 mb-5">
      {SocialShare.map((val, i) => (
        <li key={i}>
          <a href={val.link} target="_blank" rel="noreferrer">
            <i className={val.iconName}></i>
          </a>
        </li>
      ))}
    </ul>
  );
};

export default Social;
