import React, { useState } from "react";
import Modal from "react-modal";

import ModalOneBlogContent from "./modal/ModalOneBlogContent";
//import ModalTwoBlogContent from "./modal/ModalTwoBlogContent";
//import ModalThreeBlogContent from "./modal/ModalThreeBlogContent";
//import ModalFourBlogContent from "./modal/ModalFourBlogContent";
//import ModalFiveBlogContent from "./modal/ModalFiveBlogContent";
//import ModalSixBlogContent from "./modal/ModalSixBlogContent";

Modal.setAppElement("#root");

const Blog = () => {

  return (
    <>
      {/* Blog 1 Starts */}
      <div className="row grid justify-content-center">
        <article className="post-container" onClick="location.href = 'www.yoursite.com'">
          <div className="post-thumb">
            <div className="d-block position-relative overflow-hidden">
            <a href="https://thedailyinfowp.com/" target="_blank" rel="noopener noreferrer">
              <img
                src="img/blog/blog2.jpg"
                className="img-fluid"
                alt="Blog Post"
                  /> </a>
            </div>
          </div>
          {/* End .thumb */}
          <div className="post-content">
            <div className="entry-header">
                <h3>The Daily InfoWP</h3>
            </div>
            <div className="entry-content open-sans-font">
              <p>
                Get the latest Wordpress news and learn how to build a website for business or e-commerce, as well as a blog, on the platform every single day.
              </p>
            </div>
          </div>
          {/* End .post-content */}
        </article>

      </div>
       
    </>
  );
};

export default Blog;
