import React from "react";

const personalInfoContent = [
  { meta: "first name", metaInfo: "Khairul Imran"},
  { meta: "last name", metaInfo: "Yaakub" },
  { meta: "Age", metaInfo: "24 Years" },
  { meta: "Nationality", metaInfo: "Malaysia" },
  { meta: "Freelance", metaInfo: "Available" },
  { meta: "Address", metaInfo: "Kelantan" },
  { meta: "phone", metaInfo: "+60179789587" },
  { meta: "Email", metaInfo: "info@khairulimran.com" },
  { meta: "Degree", metaInfo: "Web Technology" },
  { meta: "Language", metaInfo: "Malay, English" },
];

const PersonalInfo = () => {
  return (
    <ul className="about-list list-unstyled open-sans-font">
      {personalInfoContent.map((val, i) => (
        <li key={i}>
          <span className="title">{val.meta}: </span>
          <span className="value d-block d-sm-inline-block d-lg-block d-xl-inline-block">
            {val.metaInfo}
          </span>
        </li>
      ))}
    </ul>
  );
};

export default PersonalInfo;
