import React from "react";

const experienceContent = [
    {
    year: "DEC 2020 - Present",
    position: "Junior Web Designer",
    compnayName: "Local company in Kota Bharu",
    details: `The start of my profession as a web designer, as well as providing assistance and maintenance for WordPress websites. Here, I was able to gain a deeper understanding of WordPress and design a variety of different types of websites that delight our consumers.`,
  },
  {
    year: "JUN 2020 - OKT 2020",
    position: "IT ASSISTANT",
    compnayName: "Rich Jewellery",
    details: `Working in a gold jewellery business exposed me to fresh experiences and information about the market for gold. Nevertheless, I was able to learn about WordPress for the first time and build my first ecommerce website using WordPress and Woocommerce.`,
  },
  {
    year: "Jan 2020 - April 2020",
    position: " IT ASSISTANT",
    compnayName: "Fuel Rich Sdn Bhd",
    details: `By stepping into the sector of digital marketing, I hope to learn and get new experiences. Additionally, I need to create a website for the company, design posters, and manage and keep stock.`,
  },
  {
    year: "JUL 2019 - JAN 2020",
    position: "Intern",
    compnayName: "Min House Camp Sdn Bhd",
    details: `Undergo six months of industrial training by establishing a company website, writing a blog, writing advertising on Facebook, and developing an online booking system and activity planner.`,
  },
];

const Experience = () => {
  return (
    <ul>
      {experienceContent.map((val, i) => (
        <li key={i}>
          <div className="icon">
            <i className="fa fa-briefcase"></i>
          </div>
          <span className="time open-sans-font text-uppercase">{val.year}</span>
          <h5 className="poppins-font text-uppercase">
            {val.position}
            <span className="place open-sans-font">{val.compnayName}</span>
          </h5>
          <p className="open-sans-font">{val.details}</p>
        </li>
      ))}
    </ul>
  );
};

export default Experience;
